* {
  box-sizing: border-box;
}

body {
  background-color: var(--white);
  color: var(--text);
  font-family: Roboto, sans-serif;
  font-size: 16px;
}

.menu {
  background-color: #4d04f8;
  border-radius: 4px;
  justify-content: center;
  margin: 0;
  padding: 10px;
  list-style: none;
  display: flex;
  box-shadow: 0 2px 1px -1px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 1px 3px rgba(0, 0, 0, .12);
}

button {
  letter-spacing: .04em;
  cursor: pointer;
  transition: background-color var(--transition-duration) var(--transition-timing-function);
  background-color: #fff;
  border: none;
  border-radius: 4px;
  padding: 12px;
  font-weight: 500;
  line-height: 1.5;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.more {
  justify-content: center;
  margin: 0;
  padding: 20px;
  display: flex;
}

.load-more {
  color: #fff;
  background-color: #4d04f8;
}

.load-more:hover {
  background-color: #4207ca;
}

